}

/* BRAND SECTION */
#right-side {
  width: 45%;
  display: grid;
  grid-template-areas: "brand marketing"
                        "development marketing";
                        
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.brand-strategy {
  grid-area: brand
};

.development {
  grid-area: development;
}

.section-marketing {
  grid-area: marketing;
  /* margin-left: 4rem; */
}

.section-links {
  padding-top: 1.5rem;
}

.section-links h4 {
  text-transform: uppercase;
  color: var(--primary-title-colour)
}

.section-links ul li {
  padding-top: 1rem;
  font-weight: 600;
  color: var(--secondary-title-colour);
}

.section-links li a {
  color: var(--secondary-title-colour);
  font-weight: 600;
}

.arrow-container {
  background: var(--secondary-title-colour);
  border-radius: 50%;
  width: 23px;
  height: 23px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  color: #fff;
  transition: 0.3s ease;
}

.arrow {
  color: white;
}

.section-links li a:link {
  color: var(--secondary-title-colour);
}

.section-links li a:visited {
  color: var(--secondary-title-colour);
}

.section-links li a:hover {
  color: var(--secondary-title-colour);
}

.section-links li:hover .arrow-container {
  display: inline-block;
  margin-left: 0.5rem;
  -webkit-animation: arrow-slide 0.3s ease;
          animation: arrow-slide 0.3s ease;
}

.selected .arrow-container {
  display: inline-block;
  margin-left: 0.5rem;
  -webkit-animation: arrow-slide 0.3s ease;
          animation: arrow-slide 0.3s ease;
}

/* ANIMATION */
@-webkit-keyframes arrow-slide {
  0%,
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  50% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0);
  }
}
@keyframes arrow-slide {
  0%,
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  50% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0);
  }
}


