.testimonial-container {
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding-top: 3rem;
  padding-bottom: 3rem;
  overflow: hidden;
}

.section-title {
  width: 75%;
  margin: 0 auto;
  font-size: 1.5rem;
  padding-bottom: 2rem;
}

.cards {
  width: 95%;
  height: 80vh;
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  grid-gap: 2rem;
  gap: 2rem;
}

.testimonial {
  width: 23%;
  border-radius: 10px;
  background-color: #E2E4E6;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 1rem;
  transition: 0.3s ease;
}

.quote-mark-left {
  position: absolute;
  top: 3%;
  left: 11%;
  right: 0;
  bottom: 0;
  z-index: 0;
}

.quote-mark-right {
  position: absolute;
  top: 44%;
  left: 75%;
  right: 0;
  bottom: 0;
}

.testimonial p {
  width: 70%;
  font-weight: 600;
  font-size: 1rem;
  font-style: italic;
  text-align: center;
  color: var(--secondary-title-colour);
  z-index: 1;
}

.testimonial img {
  padding-top: 2rem;
}

.user-name {
  color: var(--primary-title-colour);
  font-weight: bold;
  padding-top: 0.5rem;
}

.user-occupation {
  -webkit-columns: var(--primary-title-colour);
          columns: var(--primary-title-colour);
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.middle-testimonial-image {
  width: 35%;
  height: auto;
}

.active-testimonial {
  -webkit-transform: scale(1.15, 1.15);
          transform: scale(1.15, 1.15);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  background: var(--primary-title-colour);
  color: var(--primary-link-colour);
  box-shadow: 3px 3px 5px 1px rgb(0 0 0 / 20%);
  transition: 0.3s ease-in;
}

.active-testimonial p {
  color: var(--primary-link-colour);
  width: 80%;
}

.active-testimonial .user-name {
  color: var(--primary-link-colour);
}

.active-testimonial .quote-mark-left, .active-testimonial .quote-mark-right {
  opacity: 0.4;
}


/* card button/circles */
.button-circles {
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1.75rem;
}

.button-circles p {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  cursor: pointer;
}

.inactive-button {
  background: #C5C9CD;
  transition: 0.3s ease;
}

.active-button {
  -webkit-transform: scale(1.3, 1.3);
          transform: scale(1.3, 1.3);
  background:var(--primary-title-colour);
  transition: 0.3s ease;
}

