.arrow-container {
  background: var(--secondary-title-colour);
  border-radius: 50%;
  width: 23px;
  height: 23px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  color: #fff;
  transition: 0.3s ease;
}

.links-section li:hover .arrow-container {
