/* ANIMATION */
@-webkit-keyframes arrow-slide {
  0%,
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  50% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0);
  }
}
@keyframes arrow-slide {
  0%,
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  50% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0);
  }
}

